import React, { useRef, useContext } from 'react';
import styled from 'styled-components';
import useOnClickOutside from '../../hooks/onClickOutside';
import { MenuContext } from '../../context/navState';
import HamburgerButton from './HamburgerButton';
import { SideMenu } from './SideMenu';

import { NavLink } from 'react-router-dom'
import classes from './Header.module.scss'
import logo from "../../img/logo.png";


const MainMenu = () => {
    const node = useRef();
    const { isMenuOpen, toggleMenuMode } = useContext(MenuContext);
    useOnClickOutside(node, () => {
        // Only if menu is open
        if (isMenuOpen) {
            toggleMenuMode();
        }
    });

    return (
        <header ref={node}>
            <div className={classes.navbar}>
                <div className={classes.logo}>
                    <span><NavLink to="/" activeClassName="active">
                        <img className={classes.logo} src={logo} alt="logo"/>
                    </NavLink></span>
                </div>
                <nav>
                    <ul>
                        <li><NavLink to="/" activeClassName="active">All articles</NavLink></li>
                        {/*<li><NavLink to="/posts" activeClassName="active">All posts</NavLink></li>*/}
                        <li><NavLink to="/recent-posts" activeClassName="active">Recent</NavLink></li>
                        <li><NavLink to="/popular-posts" activeClassName="active">Popular</NavLink></li>
                        <li><NavLink to="/about-us" activeClassName="active">About us</NavLink></li>
                    </ul>
                </nav>
                <div className={classes.social_link}>
                    <div className={classes.social_link__wrap}>
                        <div className={classes.social_link_wrap__item}>
                            <a href="#"><i className="fab fa-twitter"></i></a>
                        </div>
                        <div className={classes.social_link_wrap__item}>
                            <a href="#"><i className="fab fa-facebook-f"></i></a>
                        </div>
                        <div className={classes.social_link_wrap__item}>
                            <a href="#"><i className="fab fa-pinterest-p"></i></a>
                        </div>
                    </div>
                </div>
                <HamburgerButton/>
            </div>
            <SideMenu />
        </header>
    );
};

export default MainMenu;