import React, {useState} from 'react';
import classes from './Home.module.scss'

import {connect} from "react-redux";
import NewsLatter from "./NewsLatter";
import {posts} from "../../redux/posts";
import {Link} from "react-router-dom";




function Sidebar({props, posts}) {
    const [amount, setAmount] = useState(3)
    return(

        <div id={classes.sidebar}>
            <div>
                <div className={classes.subpage_name2}>
                    Popular
                </div>
                <div>
                    {posts.slice(0, posts.length).map((post, index)=>(
                        (post.popular) ?
                        <div key={post.title} className={classes.post_wrap}>
                            <div className={classes.little_img}>
                                <Link to={"/posts/"+post.path}>
                                    <img src={post.image} alt="post-bg"/>
                                </Link>
                            </div>
                            <div className={classes.little_tost_content}>

                                    <Link to={`/posts/${post.path}`} activeClassName="active">
                                        <h3>
                                         {post.title}
                                        </h3>
                                    </Link>


                                <div className={classes.littele_post_date}>
                                    <p>{post.dateDay} {post.dateMonth} {post.dateYear}</p>
                                </div>
                            </div>

                        </div> : null
                    ))}

                </div>
            </div>

            {/*<NewsLatter/>*/}

        </div>


    )

}

const mapStateToProps = state =>{
    return {
        posts: state.postsRev
    }
}

export default connect(mapStateToProps, null)(Sidebar)
