import React from 'react'
import { Helmet } from 'react-helmet'
import classes from './AboutUs.module.scss'
import {NavLink} from "react-router-dom";

function AboutUs() {
    return (
        <div id="about-us">
            <Helmet>
                <title>Love Topics - About Us</title>
                <meta name="Blog Love Topics"/>
                <meta name="description" content="Love Topics is the online blog that focuses on the themes of relationships and love. The intent of the authors is to focus on the ways of improving the romantic relationships of couples who date for some time."/>
                <meta name="keywords" content="style, decoration, web, lifestyle, beautiful, people, communication, message, lettering, paper, greeting, romance, female, fashion, girl, network, young, person, love, modern, heart, design, concept, icon, abstract, blog, happy, art, romantic, word"/>
                <meta property="og:url"                content="https://Love-Topics.com" />
                <meta property="og:type"               content="article" />
                <meta property="og:title"              content="Love Topics - About Us" />
                <meta property="og:description"        content="Blog Love Topics - About Us" />
                <meta property="og:image"              content="https://Love-Topics.com/m1.jpg" />
            </Helmet>

            <div className={classes.breadCrumb}>
                <div className={classes.container}>
                    <nav className={classes.breadcrumb}>
                        <NavLink  className={classes.breadcrumb_item} to="/" activeClassName="active">Home</NavLink>
                        <NavLink  className={classes.breadcrumb_item} to="/about-us" activeClassName="active">About us</NavLink>
                    </nav>
                </div>
            </div>
            <div className={classes.main_content}>
                <h1>About Us</h1>
                <p>
                    Love Topics is the online blog that focuses on the themes of relationships and love. The intent of the authors is to focus on the ways of improving the romantic relationships of couples who date for some time. We also assist single people to find a love that lasts after quitting their previous relationship.
                </p>
                <p>
                    Our editorial team creates content about relationships and love with the related tips and info, romantic education, and humor that relates to the chosen topics.
                </p>
                <p>
                    For advertising parties, the Love Topics is the website that has content refreshes all the time regularly. We provide broad opportunities for the variety of content shapes. The website has different advertising widgets and materials like banners, video ads, pop-ups, and push in-window notifications.
                </p>
                <p>
                    We can get advertising rewards in different forms from the companies that ask us to place referral links to their sites. However, our opinions and conclusions are solely drawn on the unbiased analysis and review of the following products.
                </p>
            </div>
        </div>
    )
}

export default AboutUs
