import React from 'react'
import classes from "./Footer.module.scss";
import {NavLink} from "react-router-dom";
import logo from "../img/logo2.png";

function Footer() {
    return (
        <>
        <footer>
            <div className={classes.logo_footer}>
                <NavLink to="/" activeClassName="active">
                    <img className={classes.logo_footer_img} src={logo} alt="logo"/>
                    <span>Love Topics</span>
                </NavLink>
            </div>
            <nav>
                <ul className={classes.footer_nav}>
                    <li><NavLink to="/" activeClassName="active">All articles</NavLink></li>
                    <li><NavLink to="/recent-posts" activeClassName="active">Recent</NavLink></li>
                    <li><NavLink to="/popular-posts" activeClassName="active">Popular</NavLink></li>
                    <li><NavLink to="/about-us" activeClassName="active">About us</NavLink></li>
                    <li><NavLink to="/privacy-policy" activeClassName="active">Privacy policy</NavLink></li>
                    <li><NavLink to="/terms-of-use" activeClassName="active">Terms of use</NavLink></li>
                </ul>
            </nav>
            <div className={classes.social_link}>
                <div className={classes.social_link__wrap}>
                    <div className={classes.social_link_wrap__item}>
                        <a href="#"><i className="fab fa-twitter"></i></a>
                    </div>
                    <div className={classes.social_link_wrap__item}>
                        <a href="#"><i className="fab fa-facebook-f"></i></a>
                    </div>
                    <div className={classes.social_link_wrap__item}>
                        <a href="#"><i className="fab fa-pinterest-p"></i></a>
                    </div>
                </div>
            </div>
        </footer>

        <div className={classes.all_right}>
            <span>
                Copyright © 2020 All rights reserved.
            </span>
        </div>
        </>
    )
}

export default Footer
