import {SHOW_POSTS} from './types'
import {posts} from './posts'



const initialState ={
    posts,
    postsRev: posts.slice().reverse(),
    menu: false
}


export function rootReducer(state = initialState, action){
    switch (action.type) {
        case SHOW_POSTS:
            return {...state, posts: action.payload}

        default:
            return state;
    }
}
