import React from 'react'
import {useState} from 'react'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import {Helmet} from 'react-helmet'

function AllPosts({props, posts}) {
    const [amount, setAmount] = useState(2)
    function loadMore(){
        setAmount(amount + 1)
    }
    return (
        <div id="posts">
            <Helmet>
                <title></title>
            </Helmet>
            <div className="container">
                {posts.slice(0, amount).map(post=>(
                    <div key={post.title} className="post">
                        <img src={post.image} alt="post-bg"/>
                        <h3>{post.title}</h3>
                        <p><Link to={"/posts/"+post.path} >Read more</Link></p>
                    </div>
                ))}
                <button onClick={loadMore}>Load More</button>
            </div>
        </div>
    )
}

const mapStateToProps = state =>{
    return {
        posts: state
    }
}

export default connect(mapStateToProps, null)(AllPosts)
