import React, {Component} from 'react'
import {connect} from 'react-redux'
import {Helmet} from 'react-helmet'
import classes from './Post.module.scss'
import {Link, NavLink} from "react-router-dom";
import Sidebar from "../Home/Sidebar";
import "./Post.scss";
import Advertiser from "../Home/Advertiser";

import comment2 from "../../img/comment_3.png";
import comment1 from "../../img/comment_1.png";


export class Post extends Component {
  render() {
    const post = this.props.posts.find(post => post.path === this.props.globalProps.match.params.id);
    return (
      <div>
        <Helmet>
            <meta name="description" content={post.title.smallPostTxt} />
            <title>{post.title}</title>
            <meta name="Blog Love Topics"/>
            <meta name="description" content="Love Topics is the online blog that focuses on the themes of relationships and love. The intent of the authors is to focus on the ways of improving the romantic relationships of couples who date for some time."/>
            <meta name="keywords" content="style, decoration, web, lifestyle, beautiful, people, communication, message, lettering, paper, greeting, romance, female, fashion, girl, network, young, person, love, modern, heart, design, concept, icon, abstract, blog, happy, art, romantic, word"/>
            <meta property="og:url"                content="https://Love-Topics.com/posts/" />
            <meta property="og:type"               content="article" />
            <meta property="og:title"              content={post.title} />
            <meta property="og:description"        content={post.title.smallPostTxt} />
            <meta property="og:image"              content="https://Love-Topics.com/m1.jpg" />
        </Helmet>
          <div className={classes.breadCrumb}>
              <div className={classes.breadCrumb_container}>
                  <nav className={classes.breadcrumb}>
                      <NavLink  className={classes.breadcrumb_item} to="/" activeClassName="active">Home</NavLink>
                      {/*<NavLink  className={classes.breadcrumb_item} to="/recent-posts" activeClassName="active">Recent</NavLink>*/}
                      <NavLink  className={classes.breadcrumb_item} to={"/posts/"+post.path} activeClassName="active">{post.title}</NavLink>
                  </nav>
              </div>
          </div>

          <div className={classes.advertice_wrap}>
              <Advertiser/>
          </div>
        {/*<h1>{post.title}</h1>*/}
          <div className={classes.home_content_wrap}>
              <div id={classes.posts}>

                  <div className={classes.container}>
                      <div key={post.title} className={classes.post}>
                          <div className={classes.post_img_wrap}>
                              <Link to={"/posts/"+post.path}>
                                  <img src={post.image} alt="post-bg"/>
                              </Link>
                          </div>
                          <div className={classes.post_body_wrap}>

                              {(post.content.map((item, index) => (
                                      (item.type === 'h2') ? <h2 key={index} className={item.class}>{item.text}</h2> :
                                      (item.type === 'h3') ? <h3 key={index} className={item.class}>{item.text}</h3> :
                                      (item.type === 'h4') ? <h4 key={index} className={item.class}>{item.text}</h4> :
                                      //Check if paragraph contains links
                                      (item.type === 'p') ? (Array.isArray(item.text)) ?
                                      <p key={index} className={item.class}>{item.text.map(t=>(
                                      (t.href) ? <> <a target="_blank" rel="noreferrer noopener" href={t.href}>{t.hrefText}</a> &nbsp;</>: t+ ' '))}</p>
                                      : <p key={index} className={item.class}>{item.text}</p> :
                                      (item.type === 'ul') ? <ul key={index} className={item.class}>{item.text.map((liText)=>( <li key={liText}>{liText}</li> ))}</ul> :
                                      (item.type === 'img') ? <img key={index} src={item.text} alt="post-pic"/> : null
                                      )
                                  )
                              )
                              }
                              <ul className={classes.post_info}>
                                  <li><a href="#"><i className="fa fa-user"></i> {post.author.name}</a></li>
                                  <li><a href="#"><i className="fa fa-comments"></i> {post.rates.comment} Comments</a></li>
                              </ul>
                          </div>
                      </div>
                  </div>
                  <div className={classes.blog_author}>
                      <img src={post.author.image} alt={post.author.name} />
                          <div className={classes.blog_author_content}>
                              <h4>{post.author.name}</h4>
                              <p>{post.author.words}</p>
                          </div>
                  </div>

              </div>

              <div>
                  <Sidebar/>
              </div>
          </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {posts: state.posts}
}

export default connect(mapStateToProps, null)(Post)
