import React, { useState } from 'react';
import classes from './Advertiser.module.scss';

import Popup from 'reactjs-popup';



const Advertiser = () => {
    const [open, setOpen] = useState(false);
    const closeModal = () => setOpen(false);
    return (
        <div>
            <div className={classes.advertise_wrap}>
                <button type="button" className={classes.button} onClick={() => setOpen(o => !o)}>
                    Advertiser disclosure
                </button>
                | We receive referral fees
            </div>


                <Popup open={open} closeOnDocumentClick onClose={closeModal}>
                    <div className={classes.popup_bg}></div>
                    <div className={classes.modal}>
                        <a className="close" onClick={closeModal}>
                            &times;
                        </a>
                        <span>Advertiser Disclosure</span>
                        <p>The owners of this blog resource receive advertising rewards in different representations from
                            companies which issue referral links to be inserted to the content of this site. The rewards
                            affect placement and order of the products as displayed on the pages of the website. In the
                            meantime, reward is not the sole factor that leads to depicting the content in a specific way on
                            our site as stated in the present disclosure.</p>
                        <p>All findings and conclusions about the products reviewed on this blog are based on the fair
                            analysis which is not biased by all means. Moreover, the site contains various advertising
                            materials like banners, video ads, pop-ups, and push in-window notifications. As for the texts,
                            the content may include hyperbolical representations in some of its parts because of the artistic
                            manner the text is written. </p>
                    </div>
                </Popup>
            </div>

    );
};


export default Advertiser





