import React from 'react'
import classes from './Home.module.scss'
import {useState} from 'react'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import {Helmet} from 'react-helmet'
import SliderMain from "./SliderMain";
import Advertiser from "./Advertiser";
import Sidebar from "./Sidebar";
import NewsLatter from "./NewsLatter";


function Home({props, posts}) {
    const [amount, setAmount] = useState(4)
    function loadMore(){
        setAmount(amount + 1)
    }
    return (
        <>
            <Helmet>
                <title>Love Topics</title>
                <meta name="Blog Love Topics"/>
                <meta name="description" content="Love Topics is the online blog that focuses on the themes of relationships and love. The intent of the authors is to focus on the ways of improving the romantic relationships of couples who date for some time."/>
                <meta name="keywords" content="style, decoration, web, lifestyle, beautiful, people, communication, message, lettering, paper, greeting, romance, female, fashion, girl, network, young, person, love, modern, heart, design, concept, icon, abstract, blog, happy, art, romantic, word"/>
                <meta property="og:url"                content="https://Love-Topics.com" />
                <meta property="og:type"               content="article" />
                <meta property="og:title"              content="Love Topics" />
                <meta property="og:description"        content="Blog Love Topics" />
                <meta property="og:image"              content="https://Love-Topics.com/m1.jpg" />
            </Helmet>

            <div className={classes.slider_wrap}>
                <SliderMain/>
                <div className={classes.slider_title}>
                    <h1>Love Topics</h1>
                </div>
            </div>

            <div className={classes.advertice_wrap}>
                <Advertiser/>
            </div>

            <div className={classes.home_content_wrap}>
                <div id={classes.posts}>
                    <div className={classes.subpage_name}>
                        Recent
                    </div>
                    <div className={classes.container}>
                        {posts.slice(0, amount).map((post, index)=>(
                            <div key={post.title} className={classes.post}>
                                <div className={classes.post_img_wrap}>
                                    <Link to={"/posts/"+post.path}>
                                        <img src={post.image} alt="post-bg"/>
                                    </Link>

                                    <div className={classes.post_img_date}>
                                        <p>{post.dateDay}</p>
                                        <span>{post.dateMonth}</span>
                                    </div>
                                </div>
                                <div className={classes.post_body_wrap}>
                                    <Link to={"/posts/"+post.path}><h2>{post.title}</h2></Link>
                                    <p>{post.smallPostTxt}</p>
                                    <ul className={classes.post_info}>
                                        <li><a href="#"><i className="fa fa-user"></i> {post.author.name}</a></li>
                                        <li><a href="#"><i className="fa fa-comments"></i> {post.rates.comment} Comments</a></li>
                                    </ul>
                                </div>

                                {/*<p><Link to={"/posts/"+post.path} >Read more</Link></p>*/}
                            </div>
                        ))}
                        <div className={classes.btn_loadmore}>
                            {(amount<posts.length) ? <button className="load-more" onClick={loadMore}>Load more</button> : null}
                        </div>
                    </div>
                </div>

                <div>
                    <Sidebar/>
                </div>

            </div>
        </>
    )
}

const mapStateToProps = state =>{
    return {
        posts: state.postsRev
    }
}

export default connect(mapStateToProps, null)(Home)