import React, { useContext } from 'react';
import classes from "./SideMenu.module.scss"
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { MenuContext } from '../../context/navState';
import arrow from '../../img/arrow.svg';
import {NavLink} from "react-router-dom";

const Menu = styled.nav`
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0;
  margin: 10px auto 0 auto;
  z-index: 293;
  display: block;
  width: 96%;
  max-width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 0px;
  align-items: stretch;
  background-color: #ffffff;
  transform: translateY(-100%);
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);

  ${props =>
    props.open &&
    css`
      transform: translateY(0);
    `}
`;



export const SideMenu = ({ children }) => {
    const { isMenuOpen } = useContext(MenuContext);

    return <Menu  open={isMenuOpen}>{children}</Menu>;
};

SideMenu.propTypes = {
    children: PropTypes.node,
};

SideMenu.defaultProps = {
    children: (
        <>
            <div className={classes.mob_nav_wrap}>
                <ul className={classes.side_menu_Wrap}>
                    <li><NavLink to="/" activeClassName="active">All articles</NavLink></li>
                    {/*<li><NavLink to="/posts" activeClassName="active">All posts</NavLink></li>*/}
                    <li><NavLink to="/recent-posts" activeClassName="active">Recent</NavLink></li>
                    <li><NavLink to="/popular-posts" activeClassName="active">Popular</NavLink></li>
                    <li><NavLink to="/about-us" activeClassName="active">About us</NavLink></li>
                </ul>

                <div className={classes.mob_social_link__wrap}>
                    <div className={classes.mob_social_link_wrap__item}>
                        <a href="#"><i className="fab fa-twitter"></i></a>
                    </div>
                    <div className={classes.mob_social_link_wrap__item}>
                        <a href="#"><i className="fab fa-facebook-f"></i></a>
                    </div>
                    <div className={classes.mob_social_link_wrap__item}>
                        <a href="#"><i className="fab fa-pinterest-p"></i></a>
                    </div>
                </div>
            </div>
        </>
    ),
};