import React from 'react';
import classes from './NewsLatter.module.scss';


function NewsLatter() {
    return(
        <div className={classes.NewsLatter_wrap}>
            <h4 className={classes.NewsLatter_wrap_title} >Newsletter</h4>
            <form action="#">
                <div className={classes.form_group}>
                    <input type="email" className={classes.form_control} placeholder="Enter email" required="" />
                </div>
                <button className={classes.button}
                        type="submit">Subscribe
                </button>
            </form>
        </div>
    )

}

export default NewsLatter