import React from 'react';
import classes from '../Home/Home.module.scss';
import Slider from "react-slick";

import image1 from '../../img/m1.jpg';
import image2 from '../../img/m2.jpg';
import image3 from '../../img/m4.jpg';

export default function SimpleSlider(props) {
    var settings = {
        dots: false,
        infinite: true,
        speed: 2000,
        slidesToShow: 1,
        slidesToScroll: 1,
        adaptiveHeight: true,
        autoplay: true,
        autoplaySpeed: 6000,
        cssEase: "linear"
    };
    return (
        <Slider {...settings}>
            <div className={classes.slder_img_wrap}>
                <img src={image1} alt="slider img 1"/>
            </div>
            <div className={classes.slder_img_wrap}>
                <img src={image2} alt="slider img 2"/>
            </div>
            <div className={classes.slder_img_wrap}>
                <img src={image3} alt="slider img 3"/>
            </div>
        </Slider>
    );
}
